<template>
  <div class="taskExecutionSupervisionEvaluation">
    <div class="header">
      <el-input
        class="input"
        size="small"
        v-model.trim="queryForm.activityKeyword"
        placeholder="请输入营销活动内容关键词"
      ></el-input>
      <el-button size="small" type="primary" @click="getTableData">查询</el-button>
    </div>
    <div class="content">
      <!-- <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>
            <span class="text">客户关注热点趋势分析</span>
          </div>
      </div>-->
      <el-table
        class="content-table"
        border
        :data="tableData"
        :height="500"
        :row-class-name="tableRowClassName"
      >
        <el-table-column label="序号" align="center" type="index" width="70"></el-table-column>
        <el-table-column prop="activityName" label="营销活动名称"></el-table-column>
        <el-table-column align="center" label="计划参与活动的客户清单">
          <template slot-scope="scope">
            <span class="text" type="text" @click="clickCheck(scope.row)">查看</span>
          </template>
        </el-table-column>
        <el-table-column prop="executePlacePlan" label="计划执行地点"></el-table-column>
        <el-table-column prop="executeTimePlan" label="计划执行时间"></el-table-column>
        <el-table-column prop="updateTime" label="完成时间"></el-table-column>
        <el-table-column align="center" width="200" label="操作">
          <template slot-scope="scope">
            <span class="text" type="text" @click="clickDetail(scope.row)">查看参与活动客户明细</span>
          </template>
        </el-table-column>
      </el-table>
      <div class="content-page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="this.queryForm.current"
          :page-sizes="[10,20,30,40]"
          :page-size="this.queryForm.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="this.queryForm.total"
        ></el-pagination>
      </div>
    </div>
    <el-dialog
      class="check-dialog"
      title="推荐清单查看"
      :visible.sync="checkDialogVisible"
      width="80%"
      :before-close="checkDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '推荐清单查看' }}</span>
      </span>
      <div class="query-group">
        <div class="search">
          <el-select
            class="select"
            size="small"
            v-model="checkDialogForm.tagId"
            clearable
            placeholder="请选择标签"
          >
            <el-option
              v-for="item in labelOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-input
            class="input"
            size="small"
            v-model.trim="checkDialogForm.yhbh"
            placeholder="请输入用户编号"
          ></el-input>
          <el-button size="small" type="primary" @click="getCheckDialogData">查询</el-button>
        </div>
      </div>
      <el-table
        class="dialog-table"
        border
        :data="checkDialogData"
        :height="400"
        :row-class-name="tableRowClassName"
      >
        <el-table-column prop="yhbh" label="用户编号"></el-table-column>
        <el-table-column prop="yhmc" label="用电客户名称"></el-table-column>
        <el-table-column prop="yddz" label="用户地址"></el-table-column>
        <!-- <el-table-column prop="sstq" label="所属台区"></el-table-column> -->
        <!-- <el-table-column prop="tjsx" label="推荐排序"></el-table-column> -->
      </el-table>
      <div class="dialog-pagination">
        <el-pagination
          @size-change="checkDialogSizeChange"
          @current-change="checkDialogCurrentChange"
          :current-page="this.checkDialogForm.current"
          :page-sizes="[10,20,30,40]"
          :page-size="this.checkDialogForm.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="this.checkDialogForm.total"
        ></el-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="checkDialogClose">关闭</el-button>
      </span>
    </el-dialog>
    <el-dialog
      class="detail-dialog"
      title="参与活动客户标记"
      :visible.sync="detailDialogVisible"
      width="80%"
      :before-close="detailDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '参与活动客户标记' }}</span>
      </span>
      <div class="query-group">
        <div class="search">
          <el-input
            class="input"
            size="small"
            v-model.trim="detailDialogForm.yhbh"
            placeholder="请输入用户编号"
          ></el-input>
          <el-button size="small" type="primary" @click="getDetailDialogData">查询</el-button>
        </div>
      </div>
      <el-table
        class="dialog-table"
        border
        :data="detailDialogData"
        :height="400"
        :row-class-name="tableRowClassName"
      >
        <el-table-column prop="yhbh" label="用户编号"></el-table-column>
        <el-table-column prop="yhmc" label="用电客户名称"></el-table-column>
        <el-table-column prop="yddz" label="用户地址"></el-table-column>
        <!-- <el-table-column prop="sstq" label="所属台区"></el-table-column>  -->
        <el-table-column prop="algorithm" label="是否算法推荐用户">
          <template slot-scope="scope">
            {{scope.row.algorithm==="1"?'是':'否'}}
          </template>
        </el-table-column>
        <el-table-column prop="operateTime" label="操作时间"></el-table-column>
      </el-table>
      <div class="dialog-pagination">
        <el-pagination
          @size-change="detailDialogSizeChange"
          @current-change="detailDialogCurrentChange"
          :current-page="this.detailDialogForm.current"
          :page-sizes="[10,20,30,40]"
          :page-size="this.detailDialogForm.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="this.detailDialogForm.total"
        ></el-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="detailDialogClose">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { YDKH_TAG_OBJ } from '@/config'
import Interface from '@/base/mixin/precision-marketing';
export default {
  name: "taskExecutionSupervisionEvaluation",
  components: {
  },
  mixins: [Interface],
  props: {},
  data() {
    return {
      queryForm: {
        status: "4",
        activityKeyword: "",
        current: 1,
        size: 10,
        total: 0
      },
      // 表格
      tableData: [],
      // 查看框
      checkDialogVisible: false,
      checkDialogData: [{}],
      checkDialogForm: {
        tagId: "",
        yhbh: "",
        current: 1,
        size: 10,
        total: 0
      },
      labelOptions: [],
      // 详细框
      detailDialogVisible: false,
      detailDialogData: [],
      detailDialogForm: {
        yhbh: "",
        current: 1,
        size: 10,
        total: 0
      },

    };
  },
  mounted() {
    this.getTableData()
  },
  methods: {
    //表格样式
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'success-row';
      }
      return '';
    },
    // 分页
    handleSizeChange(val) {
      this.queryForm.size = val
      this.getTableData()
    },
    handleCurrentChange(val) {
      this.queryForm.current = val
      this.getTableData()
    },
    // 点击详细
    clickDetail(row) {
      this.activityId = row.activityId
      this.detailDialogForm.current = 1
      this.detailDialogForm.size = 10
      this.getDetailDialogData()
      this.detailDialogVisible = true
    },
    // 点击查看
    clickCheck(row) {
      this.activityId = row.activityId
      this.checkDialogForm.current = 1
      this.checkDialogForm.size = 10
      this.getLabelOptions()
      this.getCheckDialogData()
      this.checkDialogVisible = true
    },
    // 查看框关闭
    checkDialogClose() {
      this.checkDialogVisible = false
    },
    // 查看框分页
    checkDialogSizeChange(val) {
      this.checkDialogForm.size = val
      this.getCheckDialogData()
    },
    checkDialogCurrentChange(val) {
      this.checkDialogForm.current = val
      this.getCheckDialogData()
    },
    // 详细框关闭
    detailDialogClose() {
      this.detailDialogVisible = false
    },
    // 详细框分页
    detailDialogSizeChange(val) {
      this.detailDialogForm.size = val
      this.getDetailDialogData()
    },
    detailDialogCurrentChange(val) {
      this.detailDialogForm.current = val
      this.getDetailDialogData()
    },
    // 获取列表
    getTableData() {
      this.getActivityConfig(this.queryForm).then(res => {
        this.tableData = [...res.records]
        this.queryForm.total = res.total
      })
    },
    // 获取查看列表
    getCheckDialogData() {
      this.getRecommendCustomer({ ...this.checkDialogForm, activityId: this.activityId }).then(res => {
        this.checkDialogData = [...res.records]
        this.checkDialogForm.total = res.total
      })
    },
    // 获取详细列表
    getDetailDialogData() {

      this.getJoinCustomer({ ...this.detailDialogForm, activityId: this.activityId }).then(res => {
        // this.getJoinCustomer({ ...this.detailDialogForm, activityId: "319784888234409984" }).then(res => {
        this.detailDialogData = [...res.records]
        this.detailDialogForm.total = res.total
      })
    },
    // 获取用户标签选项
    getLabelOptions() {
      this.getLabel({
        tagobjId: YDKH_TAG_OBJ,
      }).then(res => {
        if (res.length !== 0) this.labelOptions = res.map(e => {
          return {
            value: e.bqId,
            label: e.bqName,
          }
        })
      })
    },
  }
};
</script>

<style scoped lang="scss">
.taskExecutionSupervisionEvaluation {
  ::v-deep .el-table .warning-row {
    // background: #e7f6f7;
    background: #e9f3fe;
  }
  ::v-deep .el-table {
    .success-row {
      background: rgb(244, 244, 244);
    }
    .cell {
      padding: 2px 4px;
    }
    th {
      background-color: rgb(24, 153, 250);
      .cell {
        color: #ffffff;
      }
    }
  }
  .content-border {
    background: #ffffff;
    border: 2px solid rgba(26, 132, 244, 0.2);
    border-radius: 4px;
  }
  .header {
    height: 40px;
    display: flex;
    align-items: center;
    .select {
      margin-right: 20px;
    }
    .input {
      width: 300px;
      margin-right: 20px;
    }
  }
  .content {
    .content-table {
      width: 100%;
      // height: 350px;
      .text {
        color: #1899fa;
        cursor: pointer;
      }
    }
    .content-page {
      margin-top: 10px;
      width: 100%;
      text-align: center;
    }
  }
  .detail-dialog,
  .check-dialog {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }
    .query-group {
      margin-bottom: 10px;
      .search {
        display: flex;
        align-items: center;
      }
      .select {
        margin-right: 20px;
      }
      .input {
        width: 300px;
        margin-right: 20px;
      }
    }

    .dialog-pagination {
      text-align: center;
      margin: 10px 0;
    }
  }
  .set-dialog {
    .aduitDialog-form {
      .input {
        // width: 300px;
        width: 80%;
      }
    }
  }
}
</style>
